import React, { useState, useCallback } from 'react'
import { TokenAmount, JSBI } from '@uniswap/sdk'
import Modal from '../Modal'
import { AutoColumn } from '../Column'
import styled from 'styled-components'
import { RowBetween } from '../Row'
import { TYPE, CloseIcon } from '../../theme'
import { ButtonError } from '../Button'
import { StakingInfo, useDerivedStakeInfo } from '../../state/stake/hooks'
import { useKafkaStakerContract } from '../../hooks/useContract'
import { SubmittedView, LoadingView } from '../ModalViews'
import { TransactionResponse } from '@ethersproject/providers'
import { useTransactionAdder } from '../../state/transactions/hooks'
import FormattedCurrencyAmount from '../FormattedCurrencyAmount'
//import { useActiveWeb3React } from '../../hooks'
import { maxAmountSpend } from '../../utils/maxAmountSpend'
import CurrencyInputPanel from '../CurrencyInputPanel'

const ContentWrapper = styled(AutoColumn)`
  width: 100%;
  padding: 1rem;
`

interface StakingModalProps {
  isOpen: boolean
  onDismiss: () => void
  stakingInfo: StakingInfo
}

export default function WithdrawModal({ isOpen, onDismiss, stakingInfo }: StakingModalProps) {
  //const { account } = useActiveWeb3React()

  // monitor call to help UI loading state
  const addTransaction = useTransactionAdder()
  const [hash, setHash] = useState<string | undefined>()
  const [attempting, setAttempting] = useState(false)

  function wrappedOndismiss() {
    setHash(undefined)
    setAttempting(false)
    onDismiss()
  }

  const [typedValue, setTypedValue] = useState('')
  // wrapped onUserInput to clear signatures
  const onUserInput = useCallback((typedValue: string) => {
    setTypedValue(typedValue)
  }, [])

  const totalEarned = stakingInfo.earnedLock.reduce((a, b) => a.add(b.amount), stakingInfo.earnedUnlock)
  const totalEarnedLocked = totalEarned.subtract(stakingInfo.earnedUnlock)
  // used for max input button
  const penalty = (amount: JSBI) => JSBI.divide(JSBI.multiply(amount, JSBI.BigInt(2)), JSBI.BigInt(3))
  const penaltyAmount = new TokenAmount(totalEarned.token, penalty(totalEarnedLocked.raw))
  const maxAvaliableAmount = totalEarned.subtract(penaltyAmount)
  const maxAmountInput = maxAmountSpend(maxAvaliableAmount)

  const { parsedAmount, error } = useDerivedStakeInfo(typedValue, stakingInfo.stakedAmount.token, maxAvaliableAmount)
  const amountWithPenalty =
    !parsedAmount || JSBI.lessThanOrEqual(parsedAmount.raw, stakingInfo.earnedUnlock.raw)
      ? JSBI.BigInt(0)
      : JSBI.multiply(JSBI.subtract(parsedAmount.raw, stakingInfo.earnedUnlock.raw), JSBI.BigInt(3))
  const penaltyByWithdraw = new TokenAmount(totalEarned.token, penalty(amountWithPenalty))
  const atMaxAmount = Boolean(maxAmountInput && parsedAmount?.equalTo(maxAmountInput))
  const handleMax = useCallback(() => {
    maxAmountInput && onUserInput(maxAmountInput.toExact())
  }, [maxAmountInput, onUserInput])

  const stakingContract = useKafkaStakerContract(stakingInfo.stakingRewardAddress)
  async function onWithdraw() {
    if (stakingContract && parsedAmount) {
      setAttempting(true)
      await stakingContract
        .withdrawEarned(`0x${parsedAmount.raw.toString(16)}`, { gasLimit: 300000, gasPrice: 10e9 })
        .then((response: TransactionResponse) => {
          addTransaction(response, {
            summary: `Withdraw earned KAFKA`
          })
          setHash(response.hash)
        })
        .catch((error: any) => {
          setAttempting(false)
          console.log(error)
        })
    }
  }

  return (
    <Modal isOpen={isOpen} onDismiss={wrappedOndismiss} maxHeight={90}>
      {!attempting && !hash && (
        <ContentWrapper gap="lg">
          <RowBetween>
            <TYPE.mediumHeader>Withdraw</TYPE.mediumHeader>
            <CloseIcon onClick={wrappedOndismiss} />
          </RowBetween>

          <CurrencyInputPanel
            value={typedValue}
            onUserInput={onUserInput}
            onMax={handleMax}
            showMaxButton={!atMaxAmount}
            hideBalance
            currency={stakingInfo.stakedAmount.token}
            label={'receive amount'}
            disableCurrencySelect={true}
            id="withdraw-earned-token"
          />
          {stakingInfo?.stakedAmount && (
            <AutoColumn justify="center" gap="md">
              <TYPE.body fontWeight={600} fontSize={36}>
                {parsedAmount ? <FormattedCurrencyAmount currencyAmount={parsedAmount} /> : '-'}
              </TYPE.body>
              <TYPE.body>Received KAFKA</TYPE.body>
            </AutoColumn>
          )}
          {stakingInfo?.earnedAmount && (
            <AutoColumn justify="center" gap="md">
              <TYPE.body fontWeight={600} fontSize={36}>
                {<FormattedCurrencyAmount currencyAmount={penaltyByWithdraw} />}
              </TYPE.body>
              <TYPE.body>Extra Penalty KAFKA</TYPE.body>
            </AutoColumn>
          )}
          <TYPE.subHeader style={{ textAlign: 'center' }}>
            When you withdraw, your KAFKA is claimed and your liquidity is removed from the mining pool.
          </TYPE.subHeader>
          <ButtonError disabled={!!error} error={!!error && !!stakingInfo?.stakedAmount} onClick={onWithdraw}>
            {error ?? 'Withdraw'}
          </ButtonError>
        </ContentWrapper>
      )}
      {attempting && !hash && (
        <LoadingView onDismiss={wrappedOndismiss}>
          <AutoColumn gap="12px" justify={'center'}>
            <TYPE.body fontSize={20}>Withdrawing {stakingInfo?.stakedAmount?.toSignificant(4)} Earned KAFKA</TYPE.body>
          </AutoColumn>
        </LoadingView>
      )}
      {hash && (
        <SubmittedView onDismiss={wrappedOndismiss} hash={hash}>
          <AutoColumn gap="12px" justify={'center'}>
            <TYPE.largeHeader>Transaction Submitted</TYPE.largeHeader>
            <TYPE.body fontSize={20}>Withdrew Earned KAFKA!</TYPE.body>
          </AutoColumn>
        </SubmittedView>
      )}
    </Modal>
  )
}
