import { ChainId } from '@uniswap/sdk'
import { useCallback, useState } from 'react'
import { CHAIN_CONFIG } from '../constants/networks'
import { injected } from '../connectors'

export default function useAddNetworkToMetamask(
  newChainId: ChainId
): { addNetwork: () => void; success: boolean | undefined } {
  const [success, setSuccess] = useState<boolean | undefined>()

  const addNetwork = useCallback(async () => {
    const provider = await injected.getProvider()
    if (provider && provider.isMetaMask && provider.request) {
      try {
        await provider.request({
          method: 'wallet_switchEthereumChain',
          params: [{ chainId: `0x${newChainId.toString(16)}` }]
        })
        console.log("hello")
        setSuccess(true)
      } catch (switchError) {
        if (switchError.code === 4902)
          provider
            .request({
              method: 'wallet_addEthereumChain',
              params: [CHAIN_CONFIG[newChainId]]
            })
            .then((success: boolean) => {
              setSuccess(success)
            })
            .catch(() => setSuccess(false))
      }
    } else {
      setSuccess(false)
    }
  }, [newChainId])

  return { addNetwork, success }
}
