//import { JSBI, Token } from '@uniswap/sdk'
import React, { useState, useCallback } from 'react'
import { AutoColumn } from '../../components/Column'
import { AutoRow } from '../../components/Row'
import { useActiveWeb3React } from '../../hooks'
import { TYPE } from '../../theme'
import { BodyWrapper } from '../AppBody'
import CurrencyInputPanel from '../../components/CurrencyInputPanel'

import { useCurrency } from '../../hooks/Tokens'
import { USDT, WBTC, FETH } from '../../constants'
import { wrappedCurrency, unwrappedToken } from 'utils/wrappedCurrency'

import { Text } from 'rebass'
import { ButtonError } from '../../components/Button'
import { BottomGrouping } from '../../components/swap/styleds'
import { RouteComponentProps } from 'react-router-dom'
import { currencyEquals } from '@uniswap/sdk'
import { tryParseAmount } from '../../state/swap/hooks'
import { useFaucetTokenContract } from '../../hooks/useContract'

import { useTransactionAdder } from '../../state/transactions/hooks'
import { TransactionResponse } from '@ethersproject/providers'

import { SubmittedView, LoadingView } from '../../components/ModalViews'
import Modal from '../../components/Modal'

export default function Facuet({
  match: {
    params: { currencyId }
  }
}: RouteComponentProps<{ currencyId?: string }>) {
  const { account, chainId } = useActiveWeb3React()

  const addTransaction = useTransactionAdder()
  const [hash, setHash] = useState<string | undefined>()
  const [attempting, setAttempting] = useState(false)

  const ether = useCurrency('ETH')
  const currency = useCurrency(currencyId)
  const [faucetToken, setFaucetToken] = useState(currency ?? unwrappedToken(USDT))

  const token = wrappedCurrency(faucetToken, chainId)
  const faucetContract = useFaucetTokenContract(token?.address)
  const maxFaucetAmount = currencyEquals(faucetToken, FETH)
    ? tryParseAmount('20', FETH)
    : currencyEquals(faucetToken, WBTC)
    ? tryParseAmount('2', WBTC)
    : tryParseAmount('10000', faucetToken)
  const [typedValue, setTypedValue] = useState(maxFaucetAmount ? maxFaucetAmount.toExact() : '10000')

  const parsedAmount = tryParseAmount(typedValue, faucetToken)
  const atMaxAmount = Boolean(maxFaucetAmount && parsedAmount?.equalTo(maxFaucetAmount))
  const exceedMax = Boolean(maxFaucetAmount && parsedAmount?.greaterThan(maxFaucetAmount))
  const isEther = Boolean(ether && currencyEquals(faucetToken, ether))
  const isEmpty = Boolean(!parsedAmount)
  const hasError = Boolean(!account || isEther || exceedMax || isEmpty)
  const onUserInput = useCallback((typedValue: string) => {
    setTypedValue(typedValue)
  }, [])

  const handleMax = useCallback(() => {
    maxFaucetAmount && onUserInput(maxFaucetAmount.toExact())
  }, [maxFaucetAmount, onUserInput])

  async function onFaucet() {
    if (faucetContract && parsedAmount && !isEther) {
      setAttempting(true)
      await faucetContract
        .mint(account, `0x${parsedAmount.raw.toString(16)}`, { gasLimit: 300000, gasPrice: 10e9 })
        .then((response: TransactionResponse) => {
          addTransaction(response, {
            summary: `Sent`
          })
          setHash(response.hash)
        })
        .catch((error: any) => {
          setAttempting(false)
          console.log(error)
        })
    }
  }

  function wrappedOndismiss() {
    setHash(undefined)
    setAttempting(false)
  }

  return (
    <BodyWrapper style={{ padding: 24 }}>
      <AutoColumn gap="16px">
        <AutoRow style={{ alignItems: 'center', justifyContent: 'space-between' }} gap="8px">
          <TYPE.mediumHeader>Faucet</TYPE.mediumHeader>
        </AutoRow>
        <CurrencyInputPanel
          value={typedValue}
          onUserInput={onUserInput}
          showMaxButton={!atMaxAmount}
          onMax={handleMax}
          currency={faucetToken}
          otherCurrency={ether}
          label="Amount"
          onCurrencySelect={setFaucetToken}
          id="faucet-token"
        />
      </AutoColumn>
      <BottomGrouping>
        <ButtonError error={hasError} disabled={hasError} onClick={onFaucet} id="faucet-button">
          <Text fontSize={20} fontWeight={500}>
            {exceedMax
              ? 'Too much'
              : isEther
              ? 'Not support FRA'
              : isEmpty
              ? 'Enter an amount'
              : !account
              ? 'Connect Wallet'
              : `Give me ${parsedAmount?.toSignificant(4)} ${faucetToken.symbol}` }
          </Text>
        </ButtonError>
      </BottomGrouping>
      <Modal isOpen={attempting} onDismiss={wrappedOndismiss} maxHeight={90}>
        {!hash && (
          <LoadingView onDismiss={wrappedOndismiss}>
            <AutoColumn gap="12px" justify={'center'}>
              <TYPE.body fontSize={20}>
                Sending {parsedAmount?.toSignificant(4)} {faucetToken.symbol}...
              </TYPE.body>
            </AutoColumn>
          </LoadingView>
        )}
        {hash && (
          <SubmittedView onDismiss={wrappedOndismiss} hash={hash}>
            <AutoColumn gap="12px" justify={'center'}>
              <TYPE.largeHeader>Transaction Submitted</TYPE.largeHeader>
              <TYPE.body fontSize={20}>
                {parsedAmount?.toSignificant(4)} {faucetToken.symbol} has been sent
              </TYPE.body>
            </AutoColumn>
          </SubmittedView>
        )}
      </Modal>
    </BodyWrapper>
  )
}
