import { ChainId } from '@uniswap/sdk'
import Chains from '../config/networks.json'
import { MetamaskChainParameter } from '../config/network'
import ENV from '../config/deployEnv.json'

export const CHAIN_CONFIG: { [chainId in ChainId]?:MetamaskChainParameter } = {}
Chains.filter(chain=>Object.values(ChainId).includes(Number(chain.chainId))).forEach(
  (chain)=>CHAIN_CONFIG[Number(chain.chainId) as ChainId] = chain
)

export const NETWORK_LABELS: { [chainId in ChainId]?: string } = {
  [ChainId.RINKEBY]: 'Rinkeby',
  [ChainId.ROPSTEN]: 'Ropsten',
  [ChainId.GÖRLI]: 'Görli',
  [ChainId.KOVAN]: 'Kovan',
  [ChainId.LOCAL]: 'FindoraDev'
}

export const INFOURL = ENV.INFOURL
export const NODEURL = ENV.NODEURL
export const ENV_CHAIN_ID = ENV.ChainID
export const EXPLORER = ENV.EXPLORER